.profile {
    display: grid;
    grid-template-columns: 1fr 3fr;
    grid-gap: 5%;
}

.Summary img {
    width: 100%;
}


.Summary {
    display: flex;
    flex-direction: column;
    padding-top: 10%;
    justify-content: space-around;
}

.Summary a {
    color: white;
    text-decoration: none;
}

h3 {
    color: white;
}

.comments {
    display: flex;
    flex-direction: column-reverse;
}




